#ChoixPersonnage {
    width:60%;
    padding-top:1em;
    padding-bottom:1em;
    padding-left:1em;
}
#slotPersonnage{ 
    min-width:200px;
    max-width:30%;
    text-align:justify;
    margin-top:5px;
    margin-bottom:5px;
}


.grid-container {
    display: grid;
    grid-template-columns:  33% 33% 33%;
}
#nomPerso{
    text-align: center;
    
    border-bottom:solid;
    border-color:grey;
    border-width:1px;

}

#buttonPersonnageShow{
    text-align: center;
    border-bottom:0px;
}

#show{
    margin-right: 10px;
    background-color:#3a3a3d;
    color:#efeff1;
    padding:2px;
    border-radius: 5px;
}

#name{
    font-family: fantasy;
}